* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "open sans", sans-serif;
  font-size: 20px;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  color: #222;
}

header {
  -webkit-flex-basis: 30vh;
          flex-basis: 30vh;
}

main {
  padding: 30px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

aside {
  min-width: 240px;
}

.page {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

footer {
  padding: 10px 0;
  background-color: #000;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 50px;
}

header {
  position: relative;
  overflow: hidden;
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
}

nav.main {
  padding: 0 30px 30px 0;
}

.main ul {
  list-style-type: none;
}

nav.main a {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  color: #000;
  padding: 10px;
  transition: 0.2s;
}

nav.main a:hover {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}

nav.main a.active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

nav.main li:nth-child(1) a.active {
  border-left: 10px solid green;
}

nav.main li:nth-child(2) a.active {
  border-left: 10px solid blue;
}

nav.main li:nth-child(3) a.active {
  border-left: 10px solid red;
}

nav.main li:nth-child(4) a.active {
  border-left: 10px solid orange;
}

.products h2 {
  margin-bottom: 20px;
}

.products ul {
  list-style: square;
  padding-left: 30px;
}

.products li {
  margin-bottom: 20px;
}

.products a {
  text-decoration: none;
  color: #333;
}

.products a:hover {
  text-decoration: underline;
  color: #000;
}

.contact textarea {
  font-size: 16px;
  width: 80%;
  min-height: 200px;
  margin: 20px 0;
  line-height: 1.6;
}

.contact button {
  display: block;
  border: 2px solid black;
  background-color: #fff;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 20px;
  margin: 0 20% 0 auto;
  transition: 0.3s;
}

.contact button:hover {
  color: yellow;
  background-color: #000;
}

label {
  display: block;
  font-size: 16px;
  margin: 10px 0;
  font-weight: bold;
}

label input {
  margin-left: 10px;
  font-size: 16px;
  padding: 2px;
}

button {
  background-color: #fff;
  border: 2px solid black;
  padding: 6px 12px;
  margin: 5px 0;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

button:hover {
  background-color: #000;
  color: #fff;
}

footer span {
  color: red;
}

footer p {
  font-size: 14px;
}

